import { useEffect, useState } from 'react';
import { Nav, Navbar, Button } from 'react-bootstrap';
import Title from "../Title/Title"
import Snack from "../Snack/Snack"
import axios from 'axios'; 
import snacks from '../snacks.json'


const SnacksContainer = ({

}) => {
    const apikey = "563492ad6f91700001000001d5e3e2f0b0934fe9933ab934b093d0da"
    const [imgUrl, setImgUrl] = useState("");
    const [restart, setRestart] = useState();
    const [randd, setRandd]=useState( localStorage.getItem("number")?localStorage.getItem("number"):0);
    const min = 0;
    const max = 3;
    const title = "Consejos sobre Marketing Digital"
    const [snackPack, setSnackPack] = useState();


    const getUrl = (keyword)=>{
        const url = `https://api.pexels.com/v1/search?query=${keyword}&per_page=3`
        return url;
    }
    

    const getSnackPack = ()=>{
        let rand = Math.floor(Math.random() * (max - min))+min;

        while(randd==rand){
            console.log("yes")
            rand = Math.floor(Math.random() * (max - min))+min;
        }
        localStorage.setItem("number", rand)
        setRandd(rand);
        setSnackPack(snacks.snacks[rand])
        return snacks.snacks[rand];
    }


    useEffect(async()=>{      
        const snack = getSnackPack()
        const url = getUrl(snack.keyword)
        const final = await axios.get(url, {headers:{Authorization:apikey}}).then(
            
           res=>res.data.photos.map((el, idx)=>{
            
            const result =  el.src.medium;
            const copy = {...snack.data[idx], url:result}
            return copy;
           })

       )
        setSnackPack({...snack, data:final})   
    },[restart])

      

    
   

     return (<>
     <Title title={snackPack?snackPack.title:null}/>
     <div className="container-fluid">
      <div className="row">
          {
          snackPack? snackPack.data.map((el,idx)=>
          (<Snack url = {el.url} number={idx+1} subtitle={el.subtitle} description={el.description}/>)):<h2>Cargando...</h2>}
        </div>
        </div>
    </>);
};

export default SnacksContainer;
