import { useEffect, useState } from 'react';
import { Nav, Navbar, Button } from 'react-bootstrap';
import logo from '../../logo_snacks_complete.png'


const NavBar = ({setCE}) => {
    const [show, setShow] = useState(true);

    useEffect(() => {
        return () => {
        };
    }, []);

    if (!show) {
        return null;
    }

    return (<>
        <Navbar bg="light" expand="lg" >
            <Navbar.Brand href="#home" className="logo pl-4" style={{ paddingBottom: "10px" }}><img src={logo} width="180px" /></Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mr-auto">
                </Nav>
                <Button variant="outline-dark" className="mr-4">Acerca de</Button>
                <Button  className="mr-4 btn-snacks" onClick={()=>{setCE()}}>Contact Expert</Button>
            </Navbar.Collapse>
        </Navbar>
    </>);
};

export default NavBar;
